.item {
    margin: 10px;
    color: white;
    text-shadow: 0 1px 1px black;
}

.item .like {
    text-shadow: none;
}

.item img {
    width: 50px;
    height: 50px;
    border-radius: 30px;
}

.item span {
    font-family: "Calibri Light";
    color: black;
    background-color: white;
    border-radius: 5px;
}

